import { CHIP_DIAMETER } from './bodies'

export const CANVAS_WIDTH = 800;
export const CANVAS_HEIGHT = 600;
export const SPACING = CHIP_DIAMETER * 2;

// increase top buffer to leave more open space above pegs
export const TOP_BUFFER = SPACING * 3;
// increase top buffer to leave more open space below pegs
export const BOTTOM_BUFFER = SPACING * 6;


export const ROWS = (CANVAS_HEIGHT - TOP_BUFFER - BOTTOM_BUFFER) / SPACING;

export const COLS = (CANVAS_WIDTH - SPACING * 2) / SPACING;

console.log(COLS)
